import React, {
  useState,
} from 'react';
import {
  Grid,
  InputLabel,
  Typography,
  Button,
  Link,
  Checkbox,
  ListItemText,
  List,
  makeStyles
} from '@material-ui/core';
import moment from 'moment';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrashAlt, faMinusCircle } from '@fortawesome/free-solid-svg-icons';
import {
  GridHorizontalDivider,
} from 'Components/UI/';
import {
  BasicTextField,
  BasicSelect,
} from 'Components/UI/';
import {
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { SIDE_MODAL_BLOCK_FIELDS } from '../FieldConstants'


const useStyles = makeStyles(() => ({
  keyboardDatePicker: {
    marginTop: '10px',
    marginBottom: '15px',
    width: '212px',
  },
  viewHeader: {
    color: '#272E4C',
    textAlign: 'left',
    opacity: 0.6,
    letterSpacing: 0,
    textAlignLast: 'left',
    fontSize: '12px',
  },
  viewHeaderValue: {
    color: '#272E4C',
    textAlign: 'left',
    opacity: 1,
    letterSpacing: 0,
    textAlignLast: 'left',
    fontSize: '14px',
    fontWeight: 600,
  },
  deleteButton: {
    textDecoration: 'underline',
    color: '#272E4C',
    opacity: 0.6,
    cursor: 'pointer',
  },
  checkbox: {
    '&.Mui-checked': {
      color: '#1e2071',
    },
  },
}));

interface SideModalBodyProps {
  formFields?: any;
  invalidFields?: any[];
  refreshData?: (data: any) => void;



  verticalSpacing?: string;
  deleteIcon?: string; // Text next to delete icon, unhides delete icon
  deactivateIcon?: string; // Text next to deactivate icon, unhides deactivate icon
  disableSaveButton?: boolean;



  delete?: (data: any) => void;
  deactivate?: (data: any) => void;



  handleClose: () => void;
  handleSave?: (formFields?: any) => void;



  mode: 'view' | 'edit' | 'add' | ''; // modes of side modal block 
  formName: string; // relates to FieldConstants.tsx. Set value to ICD10, CPT, or etc.
  showRequiredFields?: boolean;



  dynamicDropdownChoices?: any[]; // for regular dropdown field
  checkBoxWithDropDownFieldsInitState?: boolean; // initial state of checkbox with 
  // datePairField1MinDate?: Date | string; // min date for first picker of date pair fields
  // datePairField1MaxDate?: Date | string; // max date for first picker of date pair fields
  // datePairField2MinDate?: Date | string; // min date for second picker of date pair fields
  // datePairField2MaxDate?: Date | string; // max date for second picker of date pair fields
}

export const SideModalBody = (props: SideModalBodyProps) => {

  const { formName, formFields, handleClose, invalidFields, showRequiredFields, verticalSpacing } = props;
  const classes = useStyles();

  const [
    checkBoxWithDropDownChecked,
    setcheckBoxWithDropDownChecked
  ] = useState(props.checkBoxWithDropDownFieldsInitState ? true : false);

  const {
    requiredFields,
    excludeFields,
    includeFields,
    textPairFields,
    regularTextBoxFields,
    regularDropDownFields,
    largeTextBoxFields,
    dateFields,
    datePairFields,
    checkBoxWithDropDownFields,
  } = SIDE_MODAL_BLOCK_FIELDS[formName][props.mode === 'add' || props.mode === 'edit' ? 'editOrAdd' : props.mode]





































  /*****************************  
           FORM ACTIONS 
  ******************************/
  const handleFormChange = (data: any) => {
    if (props.refreshData) {
      props.refreshData(data)
    }
  }

  const handleDateChange = (date: any, form_fields: any, field: string) => {
    try {
      if (date && date.toDate() instanceof Date && !isNaN(date.toDate().valueOf())) {
        const iso_date = moment(date).toDate()
        handleFormChange(Object.assign(form_fields, { [field]: iso_date }))
      } else {
        handleFormChange(Object.assign(form_fields, { [field]: '' }))
      }
    } catch {
      handleFormChange(Object.assign(form_fields, { [field]: '' }))
    } finally {
    }
  };






































  /*****************************  
        LIST FOR VIEW MODE 
  ******************************/
  const renderViewItems = (field: string) => {
    const excluded_fields = excludeFields //Object.keys(excludeFields)
    if (excluded_fields.includes(field)) return null;
    let label: string | undefined = '';
    let value: string | undefined = '';


    const renderSingleItem = () => {
      return (
        <div key={field + ' div'}>
          <ListItemText key={field + 'date'}>
            <div key={field + ' Header'} className={classes.viewHeader}>{label ? label.toUpperCase() : ''}</div>
            <div key={field + ' Value'} className={classes.viewHeaderValue}>{formFields && formFields[field] ? value : '-'}</div>
            <br />
          </ListItemText>
        </div>
      );
    }

    const renderPairItems = (pairObj: any) => {
      let label1: string = pairObj.title1;
      let value1: any = formFields && formFields[pairObj.name1] ? formFields[pairObj.name1] : '';
      let label2: string = pairObj.title2;
      let value2: any = formFields && formFields[pairObj.name2] ? formFields[pairObj.name2] : '';

      if (pairObj.date1) value1 = value1 ? moment(value1).format('LL') : '';
      if (pairObj.date2) value2 = value2 ? moment(value2).format('LL') : '';

      return (
        <ListItemText key={'pair values: ' + label1 + ' ' + label2}>
          <Grid container>
            <Grid item xs={6}>
              <div key={label1 + ' Header'} className={classes.viewHeader}>{label1 ? label1.toUpperCase() : ''}</div>
              <div key={label1 + ' Value'} className={classes.viewHeaderValue}>{value1}</div>
              <br />
            </Grid>
            <Grid item xs={6}>
              <div key={label2 + ' Header'} className={classes.viewHeader}>{label2 ? label2.toUpperCase() : ''}</div>
              <div key={label2 + ' Value'} className={classes.viewHeaderValue}>{value2}</div>
              <br />
            </Grid>
          </Grid>
        </ListItemText>
      );
    }

    if (includeFields && Array.isArray(includeFields) && includeFields.some(item => item.name === field)) {
      if (formName && ['ICD10', 'CPT'].includes(formName) && field === 'gender' && !formFields[field]) return null;
      label = includeFields.find(item => item.name === field)['title'];
      value = formFields[field];
      return renderSingleItem();
    } else if (dateFields && Array.isArray(dateFields) && dateFields.some(item => item.name === field)) {
      label = dateFields.find(item => item.name === field)['title'];
      value = moment(formFields[field]).format('LL');
      return renderSingleItem();
    } else if (textPairFields && Array.isArray(textPairFields) && textPairFields.some(item => item.name1 === field)) {
      const current_pair: any = textPairFields.find(item => item.name1 === field);
      if (!current_pair) return null;
      return renderPairItems(current_pair);
    } else {
      return null;
    }
  }


  const renderViewMode = () => {
    const all_fields = Object.keys(formFields)
    return (
      <List>
        {all_fields.map(field => renderViewItems(field))}
      </List>
    )
  }



































  /*****************************  
    FORM FOR EDIT OR ADD MODE 
  ******************************/
  const renderRegularTextBoxFields = (field: string, requiredFieldsIndicator: boolean) => {
    const label: string = regularTextBoxFields.find(item => item.name === field)['title'];
    return (
      <Grid item xs={12}>
        <BasicTextField
          labelFontSize="14px"
          id={field}
          label={(requiredFieldsIndicator ? '**' : '') + label}
          value={formFields && formFields[field] ? formFields[field] : ''}
          error={invalidFields && invalidFields.includes(field)}
          onChange={(e: any) => {
            handleFormChange(Object.assign(formFields, { [field]: e.target.value }))
          }}
        />
      </Grid>
    )
  }


  const renderLargeTextBoxFields = (field: string, requiredFieldsIndicator: boolean) => {
    const label: string = largeTextBoxFields.find(item => item.name === field)['title'];
    return (
      <Grid item xs={12}>
        <BasicTextField
          labelFontSize="14px"
          id={field}
          rows="3"
          label={(requiredFieldsIndicator ? '**' : '') + label}
          value={formFields && formFields[field] ? formFields[field] : ''}
          error={invalidFields !== undefined && invalidFields.includes(field)}
          onChange={(e: any) => {
            handleFormChange(Object.assign(formFields, { [field]: e.target.value }))
          }}
        />
      </Grid>
    )
  }


  const renderDatePairFields = (pairObj: any) => {
    return (
      <Grid container>
        <Grid>
          <InputLabel htmlFor={pairObj.name1} style={{ fontSize: '14px' }}>
            {(pairObj.required1 && props.showRequiredFields ? '**' : '') + pairObj.title1}
          </InputLabel>
          <KeyboardDatePicker
            allowKeyboardControl
            // inputProps={{
            //   disabled: true
            // }}
            id={pairObj.name1}
            variant="dialog"
            format="MM/DD/YYYY"
            inputVariant="outlined"
            maxDate={pairObj.minMaxRelationship && moment(formFields[pairObj.name2]).isValid() ? formFields[pairObj.name2] : undefined}
            error={invalidFields !== undefined && invalidFields.includes(pairObj.name1)}
            className={classes.keyboardDatePicker}
            value={formFields ? moment(formFields[pairObj.name1]).format('MM-DD-YY').toString() : ''}
            invalidDateMessage=""
            onChange={date => handleDateChange(date, formFields, pairObj.name1)}
          />
        </Grid>
        <Typography display="inline">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Typography>
        <Grid>
          <InputLabel htmlFor={pairObj.name2} style={{ fontSize: '14px' }}>
            {(pairObj.required2 && props.showRequiredFields ? '**' : '') + pairObj.title2}
          </InputLabel>
          <KeyboardDatePicker
            allowKeyboardControl
            id={pairObj.name2}
            // inputProps={{
            //   disabled: true
            // }}
            variant="dialog"
            format="MM/DD/YYYY"
            inputVariant="outlined"
            minDate={pairObj.minMaxRelationship && moment(formFields[pairObj.name1]).isValid() ? formFields[pairObj.name1] : undefined}
            error={invalidFields && invalidFields.includes(pairObj.name2)}
            className={classes.keyboardDatePicker}
            value={formFields && formFields[pairObj.name2] ?
              moment(formFields[pairObj.name2]).format('MM/DD/YY').toString() : ''}
            invalidDateMessage=""
            onChange={date => {
              handleDateChange(date, formFields, pairObj.name2)
            }}
          />
        </Grid>
      </Grid>
    )
  }


  const renderDateFields = (field: string, requiredFieldsIndicator: boolean) => {
    const label: string = dateFields.find(item => item.name === field)['title'];
    return (
      <Grid item xs={12}>
        <InputLabel htmlFor={field} style={{ fontSize: '14px' }}>{(requiredFieldsIndicator ? '**' : '') + label}</InputLabel>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <KeyboardDatePicker
            id={field}
            variant="dialog"
            format="MM/DD/YYYY"
            inputVariant="outlined"
            className={classes.keyboardDatePicker}
            error={invalidFields && invalidFields.includes(field)}
            value={formFields && formFields[field] ? moment(formFields[field]).format('MM-DD-YY').toString() : ''}
            invalidDateMessage=""
            onChange={date => handleDateChange(date, formFields, field)}
          />
        </div>
      </Grid>
    )
  }


  const renderCheckboxWithDropdownFields = (pairObj: any) => {
    return (
      <Grid item xs={12}>
        <Checkbox style={{ marginTop: '-10px', marginLeft: '-10px', marginBottom: '-1px' }}
          color="primary"
          classes={{ colorPrimary: classes.checkbox, }}
          checked={checkBoxWithDropDownChecked}
          onChange={(event: React.ChangeEvent<HTMLInputElement>, ) => {
            setcheckBoxWithDropDownChecked(event.target.checked);
            handleFormChange(Object.assign(formFields, { [pairObj.checkboxName]: event.target.checked }))
          }}
        />
        {pairObj.checkboxTitle}
        {checkBoxWithDropDownChecked ? (
          <React.Fragment>
            <BasicSelect
              marginTop='10px'
              marginBottom='10px'
              labelFontSize='14px'
              id={pairObj.dropdownName}
              label={pairObj.dropdownTitle}
              error={invalidFields !== undefined && invalidFields.includes(pairObj.dropdownName)}
              menuItems={pairObj.dropdownChoices}
              value={formFields && formFields[pairObj.dropdownName] ? formFields[pairObj.dropdownName] : '-'}
              onChange={(e: any) => handleFormChange(Object.assign(formFields, { [pairObj.dropdownName]: e.target.value }))}
              width="230px"
            />
          </React.Fragment>
        ) : null}
      </Grid>
    )
  }


  const renderRegularDropdownFields = (field: string, requiredFieldsIndicator: boolean) => {
    const label: string = regularDropDownFields.find(item => item.name === field)['title'];
    return (
      <React.Fragment>
        <BasicSelect
          marginTop='10px'
          marginBottom='10px'
          labelFontSize='14px'
          id={field}
          label={(requiredFieldsIndicator ? '**' : '') + label}
          error={invalidFields && invalidFields.includes(field)}
          menuItems={props.dynamicDropdownChoices ? props.dynamicDropdownChoices : []}
          value={formFields && formFields[field] ? formFields[field] : '-'}
          onChange={(e: any) => handleFormChange(Object.assign(formFields, { [field]: e.target.value }))}
          width="100%"
          maxWidth="100%"
        />
      </React.Fragment>
    )
  }


  const renderEditOrAddForm = (field: string) => {

    let renderRegularTextBoxes: boolean = false;
    let renderLargeTextBoxes: boolean = false;
    let renderDatePairPickers: boolean = false;
    let datePairFieldsObj: any = {};
    let renderDatePickers: boolean = false;
    let renderCheckboxWithDropdown: boolean = false;
    let checkboxWithDropdownObj: any = {};
    let renderRegularDropDown: boolean = false;


    const render = () => {
      return (
        <div key={`block-body-${field}`}>
          {renderRegularTextBoxes ? renderRegularTextBoxFields(field, requiredFieldsIndicator)
            : renderLargeTextBoxes ? renderLargeTextBoxFields(field, requiredFieldsIndicator)
              : renderDatePairPickers && Object.entries(datePairFieldsObj).length ? renderDatePairFields(datePairFieldsObj)
                : renderDatePickers ? renderDateFields(field, requiredFieldsIndicator)
                  : renderCheckboxWithDropdown && Object.entries(checkboxWithDropdownObj).length ? renderCheckboxWithDropdownFields(checkboxWithDropdownObj)
                    : renderRegularDropDown ? renderRegularDropdownFields(field, requiredFieldsIndicator) : null}
          <GridHorizontalDivider height={verticalSpacing ? verticalSpacing : "0px"} />
        </div>
      )
    }

    const requiredFieldsIndicator: boolean = showRequiredFields && requiredFields && Array.isArray(requiredFields) && requiredFields.includes(field);

    const exclude_fields: string[] = excludeFields ? excludeFields : []
    if (exclude_fields.includes(field)) return null;


    renderRegularTextBoxes = regularTextBoxFields && Array.isArray(regularTextBoxFields) && regularTextBoxFields.some(item => item.name === field);
    if (renderRegularTextBoxes) return render();

    renderLargeTextBoxes = largeTextBoxFields && Array.isArray(largeTextBoxFields) && largeTextBoxFields.some(item => item.name === field);
    if (renderLargeTextBoxes) return render();

    renderDatePickers = dateFields && Array.isArray(dateFields) && dateFields.some(item => item.name === field);
    if (renderDatePickers) return render();

    renderRegularDropDown = regularDropDownFields && Array.isArray(regularDropDownFields) && regularDropDownFields.some(item => item.name === field);
    if (renderRegularDropDown) return render();

    renderDatePairPickers = datePairFields && Array.isArray(datePairFields) && datePairFields.some(item => item.name1 === field);


    if (renderDatePairPickers) {
      let temp: any = datePairFields.find(item => item.name1 === field);
      datePairFieldsObj = { ...temp };
      if (requiredFields && Array.isArray(requiredFields))
        requiredFields.forEach(item => {
          if (item === temp.name1) {
            datePairFieldsObj['required1'] = true;
          } else if (item === temp.name2) {
            datePairFieldsObj['required2'] = true;
          }
        });
      return render();
    }

    renderCheckboxWithDropdown = checkBoxWithDropDownFields && Array.isArray(checkBoxWithDropDownFields) && checkBoxWithDropDownFields.some(item => item.checkboxName === field);

    if (renderCheckboxWithDropdown) {
      let temp: any = checkBoxWithDropDownFields.find(item => item.checkboxName === field);
      checkboxWithDropdownObj = { ...temp };
      if (requiredFields && Array.isArray(requiredFields))
        requiredFields.forEach(item => {
          if (item === temp.checkboxName) {
            checkboxWithDropdownObj['checkboxRequired'] = true;
          } else if (item === temp.dropdownName) {
            checkboxWithDropdownObj['dropdownRequired'] = true;
          }
        });
      return render();
    }

    return null;
  }

  const renderEditOrAddMode = () => {
    const all_fields = Object.keys(formFields)
    return (
      <>
        {showRequiredFields && props.mode === 'add' ? (
          <>
            <span style={{ color: 'red' }}><i>**Required Fields</i></span>
            <GridHorizontalDivider height="20px" />
          </>

        ) : null}
        {all_fields.map(field => renderEditOrAddForm(field))}
        {showRequiredFields && props.mode === 'edit' ? (
          <>
            <span><i>**Required Fields</i></span>
            <GridHorizontalDivider height="20px" />
          </>
        ) : null}
      </>
    )
  }



































  /*****************************  
           MAIN RENDER 
  ******************************/
  return (
    <div style={{ maxWidth: '450px' }}>
      {props.mode === 'view' ?
        renderViewMode()
        : props.mode === 'edit' || props.mode === 'add' ? (
          renderEditOrAddMode()
        ) : null}

      {props.mode === 'edit' && props.deleteIcon && props.deleteIcon !== '' ? (
        <Link className={classes.deleteButton} style={{ marginTop: '-10px', marginBottom: '-10px' }}
          onClick={() => {
            if (props.delete) {
              props.delete(formFields)
            }
          }}>
          <FontAwesomeIcon icon={faTrashAlt} style={{ marginRight: '10px' }} />
          {props.deleteIcon}
        </Link>
      ) : null}

      {props.mode === 'edit' && props.deactivateIcon && props.deactivateIcon !== '' ? (
        <Link
          className={classes.deleteButton}
          onClick={() => {
            if (props.deactivate) {
              props.deactivate(formFields)
            }
          }} style={{ marginTop: '-10px', marginBottom: '-10px' }}>
          <FontAwesomeIcon icon={faMinusCircle} style={{ marginRight: '3px', textDecoration: 'underlined' }} />
          {props.deactivateIcon}
        </Link>
      ) : null}

      {/* <GridHorizontalDivider height="10px" /> */}
      {props.mode === 'edit' || props.mode === 'add' ? (
        <Grid container spacing={2} justify="center" alignItems="flex-start">
          <Grid item xs={4}>
            <Button onClick={handleClose} variant="outlined" color="default" fullWidth>Cancel</Button>
          </Grid>
          <Grid item xs={4}>
            <Button
              onClick={
                () => {
                  if (props.handleSave)
                    props.handleSave(formFields);
                }}
              variant="contained"
              color="primary"
              fullWidth
              disabled={props.disableSaveButton ? props.disableSaveButton : false}
            >
              {props.mode === 'add' ? 'Add' : props.mode === 'edit' ? 'Update' : null}
            </Button>
          </Grid>
        </Grid>
      ) : null}
    </div>
  );
};

export default SideModalBody;
